.App {
  width: 50%;
  height: 100%;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: #fff;
  padding: 3rem;
  border-radius: 20px;
}
.todo-editor .todo-input {
  display: flex;
  gap: 10px;
}
.todo-editor .todo-input input {
  width: 500px;
  flex: 1;
  padding: 15px 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
}
.todo-editor .todo-input input:focus {
  border: 1px solid burlywood
}
.todo-editor .todo-input button {
  border: none;
  border-radius: 5px;
  padding: 0 15px;
  color: #fff;
  background-color: burlywood;
  cursor: pointer;
}
.todo-list h3 {
  margin: 0;
  margin-bottom: 10px;
}
.todo-list .todo-list-length {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}
.todo-list .todo-list-length span {
  font-size: 12px;
  color: burlywood;
  font-weight: bold;
}
.todo-list .search {
  width: 100%;
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 30px;
  outline: none;
}
.todo-list .search:focus {
  border-bottom: 1px solid burlywood;
}
.todo-item {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #eee;
  padding: 10px 0;
  gap: 10px;
  
}
.content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;  
}
.content svg {
  color: burlywood;
  cursor: pointer;
}
.content.isDone .todo-col {
  text-decoration: line-through;
  color: gray;
  font-weight: normal;
}
.todo-item .date-col {
  font-size: 12px;
  color: gray;
}
.todo-item .todo-col {
  flex: 1;
  font-weight: bold;
  color: #333;
}
@media all and (width:768px) {
  .App {
    width: 100%;
  }
  .todo-editor .todo-input input {
    width: 100%;
  }
}